.titleBox {
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    img {
        width: 30px;
        height: auto;
        margin-right: 20px;
    }

    h1 {
        color: var(--primaryTitle);
        font-weight: 900;
    }
}

.mainContent {
    position: relative;
    width: 100%;
    height: calc(70vh);
    overflow-y: scroll;

