nav {
    width: 30%;
    height: 100vh;
    top: 0;
    left: 0;
    position: fixed;

    .poly1 {
        width: 100%;
        height: 100vh;
        background-color: #51566C;
        clip-path: polygon(0 0, 20% 0, 80% 100%, 0% 100%);
        position: absolute;
        z-index: 10;
    }

    .poly2 {
        width: 100%;
        height: 100vh;
        background-color: #DD5151;
        clip-path: polygon(0 0, 100% 0, 50% 100%, 0% 100%);
        position: absolute;
        z-index: 20;
    }

    .poly3 {
        width: 100%;
        height: 100vh;
        background: rgb(44,46,58);
        background: linear-gradient(180deg, rgba(44,46,58,1) 0%, rgba(74,79,99,1) 100%);
        clip-path: polygon(0 0, 96% 0, 50% 100%, 0% 100%);
        position: absolute;
        z-index: 30;
    }

    .navContent {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 10% 0 10% 15%;
        position: absolute;
        z-index: 40;

        .buttonArea {

        }
    }
}